import { fabric } from "fabric"

export default {
    data() {
        return {
            // ctx: null,
            canvas: null,
            alive: false,
        }
    },
    mounted() {
        this.alive = true
        this.canvas = new fabric.Canvas(this.$refs.canvas)
        // this.ctx = this.canvas.getContext('2d')
        window.addEventListener("resize", this.resize_event)
        this.resize()
    },
    destroyed() {
        this.alive = false
        this.canvas.dispose()
        window.removeEventListener("resize", this.resize_event)
    },
    methods: {
        resize_event() {
            this.resize()
        },
        resize() {
            // console.log("resize from mixin")
        },

        updatePolygon(polygon) {
            const { width, height, left, top } = polygon._calcDimensions()
            const nwidth = Math.max(Math.abs(left), Math.abs(left + width)) * 2
            const nheight = Math.max(Math.abs(top), Math.abs(top + height)) * 2
            polygon
                .set({
                    width: nwidth,
                    height: nheight,
                    // pathOffset: new fabric.Point(left + nwidth / 2, top + nheight / 2),
                })
                .setCoords()
        },


    },
}