function throttle(callback, delay) {
    let last_call = null
    let timer_id = null
    let last_output = null

    function throttled() {
        const ctx = this
        const args = arguments
        let now = new Date().getTime()

        clearTimeout(timer_id)

        if (!last_call || now - last_call > delay) {

            last_output = callback.apply(ctx, args)
            last_call = now

        } else {
            timer_id = setTimeout(() => {
                last_output = callback.apply(ctx, args)
                last_call = null
            }, delay)
        }
        return last_output
    }
    return throttled
}


function single_promise(callback) {
    let call_id = null
    let busy = false
    function single() {
        const ctx = this
        const args = arguments

        clearTimeout(call_id)
        
        if (busy) {
            call_id = setTimeout(() => {
                single.apply(ctx, args)
            }, 0)
            return
        }

        busy = true

        return new Promise(done => {
            callback.apply(ctx, args).then(() => {
                done(arguments)
            }).finally(() => {
                busy = false
            })
        })
    }
    return single
}


export {
    throttle, single_promise
}