import * as tf from "@tensorflow/tfjs"
import { extreme_angle } from "./layout"

function rectangular(x, y, w, h, samples) {
  let d = tf.range(0, samples + 1).div(samples).sub(0.5)
  let fd = d.reverse()

  d = d.slice(0, samples)
  fd = fd.slice(0, samples)

  let o = tf.ones([samples]).mul(0.5)
  let no = o.mul(-1)
  let px = tf.concat1d([d, o, fd, no]).mul(w).add(x)
  let py = tf.concat1d([no, d, o, fd]).mul(h).add(y)
  return tf.concat2d([px.expandDims(0), py.expandDims(0)])
}

function circular(x, y, w, h, samples) {
  let a = tf.range(0, samples).div(samples).mul(Math.PI * 2)
  let px = tf.cos(a).mul(w / 2).add(x)
  let py = tf.sin(a).mul(h / 2).add(y)
  return tf.concat2d([px.expandDims(0), py.expandDims(0)])
}


function shape2points(shape, scale) {
  let points = []
  for (let i = 0; i < shape[0].length; i++) {
    points.push({
      x: shape[0][i] * scale,
      y: -shape[1][i] * scale,
    })
  }
  return points
}


function span_extreme_points(k, scale) {
  let [id_max, id_min] = extreme_angle(k)
  return [
    { x: 0, y: 0 },
    {
      x: k[0][id_max] * scale,
      y: -k[1][id_max] * scale,
    },
    {
      x: k[0][id_min] * scale,
      y: -k[1][id_min] * scale,
    },
  ]
}


function cross(o, a, b) {
  return (a[0] - o[0]) * (b[1] - o[1]) - (a[1] - o[1]) * (b[0] - o[0])
}

function convhull(shape) {
  let p = []
  for (let i = 0; i < shape[0].length; i++) p.push(i)

  p.sort((a, b) => {
    if (shape[0][a] < shape[0][b]) return -1
    if (shape[0][a] > shape[0][b]) return 1
    return shape[1][a] - shape[1][b]
  })

  let U = []
  let L = []

  for (let i of p) {
    while (L.length >= 2 && cross(
      [shape[0][L[L.length - 2]], shape[1][L[L.length - 2]]],
      [shape[0][L[L.length - 1]], shape[1][L[L.length - 1]]],
      [shape[0][i], shape[1][i]],
    ) <= 0) L.pop()
    while (L.length >= 2 && cross(
      [shape[0][U[U.length - 2]], shape[1][U[U.length - 2]]],
      [shape[0][U[U.length - 1]], shape[1][U[U.length - 1]]],
      [shape[0][i], shape[1][i]],
    ) >= 0) U.pop()
    L.push(i)
    U.push(i)
  }

  return L.concat(U.slice(1, -1).reverse())
}

function convhullshape(shape) {
  let shape_tensor = tf.cast(shape, 'float32')
  shape = shape_tensor.arraySync()
  let index = convhull(shape)
  return shape_tensor.gather(index, 1)
}

const convshape = function (shape1, shape2) {
  let A = tf.expandDims(shape1, 1)
  let B = tf.expandDims(shape2, 2)
  return convhullshape(A.add(B).reshape([2, -1]))
}


export {
  rectangular, circular,
  shape2points, span_extreme_points,
  convhull, convhullshape, convshape
}