<template>
  <div ref="base">
    <canvas class="canvas" width="500" height="500" ref="canvas"> </canvas>
    <div hidden>
      {{ g1_props }}
      {{ g3_props }}
      {{ points }}
      {{ circle_props }}
    </div>
  </div>
</template>

<script>
import canvas from "./canvas.js";
import { fabric } from "fabric";
import { throttle } from "./lib/throttle.js";
import { shape2points, span_extreme_points } from "./lib/shape.js";

export default {
  props: {
    g1: Array,
    g3: Array,
    k0: Number,
    k1: Array,
    k2: Array,
    k3: Array,
    material: Number,
  },

  mixins: [canvas],

  data() {
    return {
      // typings
      /**@type {fabric.Canvas} */
      canvas: null,
      // shapes
      k1_shape: null,
      k2_shape: null,
      k3_shape: null,
      circle1: null,
      circle2: null,
      k2_span_shape: null,
      k3_span_shape: null,
    };
  },

  // SETUP -------------------------------------
  mounted() {
    this.canvas.add(
      (this.k1_shape = new fabric.Polygon([], {
        fill: "#37474f",
        left: 0,
        top: 0,
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.k2_span_shape = new fabric.Polygon([], {
        fill: "#ffcc8080",
        left: 0,
        top: 0,
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.k3_span_shape = new fabric.Polygon([], {
        fill: "#90caf980",
        left: 0,
        top: 0,
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.circle1 = new fabric.Circle({
        stroke: "black",
        fill: null,
        strokeWidth: 2,
        radius: 1000,
        selectable: false,
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.circle2 = new fabric.Circle({
        stroke: "black",
        fill: null,
        strokeWidth: 2,
        radius: 1000,
        selectable: false,
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.set("scaleY", -1);

    this.k2_shape = this.create_k_patch("#ff9800", this.g1_update);
    this.k3_shape = this.create_k_patch("#2196f3", this.g3_update);

    this.canvas.absolutePan(
      new fabric.Point(-this.canvas.width / 2, -this.canvas.height / 2)
    );
    this.canvas.selection = false;
  },
  methods: {
    resize() {
      let s = this.$refs.base.getBoundingClientRect().width;
      // s = Math.min(s, 500);
      this.canvas.setWidth(s);
      this.canvas.setHeight(s);
      this.canvas.calcOffset();
      this.canvas.absolutePan(
        new fabric.Point(-this.canvas.width / 2, -this.canvas.height / 2)
      );
    },

    create_k_patch(color, callback) {
      let shape = new fabric.Polygon([], {
        fill: color,
        originX: "center",
        originY: "center",
        stroke: "white",
        strokeWidth: 2,
        lockRotation: true,
        lockScalingX: true,
        lockScalingY: true,
        hasControls: false,
        // width: 100,
        // height: 100,
      })
        .on("modified", callback)
        .on("moving", callback);
      this.canvas.add(shape);

      return shape;
    },
    // update events
    g1_update: throttle(function () {
      this.$emit("update:g1", [
        this.k2_shape.get("left") / this.scaling,
        -this.k2_shape.get("top") / this.scaling,
      ]);
    }, 20),
    g3_update: throttle(function () {
      this.$emit("update:g3", [
        -this.k3_shape.get("left") / this.scaling,
        this.k3_shape.get("top") / this.scaling,
      ]);
    }, 20),
  },
  computed: {
    k0_mat() {
      return this.k0 * this.material;
    },
    scaling() {
      return (this.canvas.width / 2 / this.k0_mat) * 0.8;
    },

    // K vector fields
    g1_props() {
      if (!this.k2_shape) return 0;
      this.k2_shape.set("left", this.g1[0] * this.scaling);
      this.k2_shape.set("top", -this.g1[1] * this.scaling);
      this.k2_shape.setCoords();
      this.k2_span_shape.set(
        "points",
        span_extreme_points(this.k2, this.scaling)
      );
      this.updatePolygon(this.k2_span_shape);
      this.canvas.requestRenderAll();
      return 0;
    },

    g3_props() {
      if (!this.k3_shape) return 0;
      this.k3_shape.set("left", -this.g3[0] * this.scaling);
      this.k3_shape.set("top", this.g3[1] * this.scaling);
      this.k3_shape.setCoords();
      this.k3_span_shape.set(
        "points",
        span_extreme_points(this.k3, this.scaling)
      );
      this.updatePolygon(this.k3_span_shape);
      this.canvas.requestRenderAll();
      return 0;
    },

    points() {
      if (!this.k1_shape) return 0;
      let points = shape2points(this.k1, this.scaling);
      this.k1_shape.set("points", points);
      this.k2_shape.set("points", points);
      this.k3_shape.set("points", points);
      this.updatePolygon(this.k1_shape);
      this.updatePolygon(this.k2_shape);
      this.updatePolygon(this.k3_shape);

      this.canvas.requestRenderAll();
      return 0;
    },

    // donuts
    circle_props() {
      if (!this.circle1) return 0;
      this.circle1.set("radius", this.k0 * this.scaling);
      this.circle2.set("radius", this.k0_mat * this.scaling);
      this.circle1.setCoords();
      this.circle2.setCoords();
      this.canvas.requestRenderAll();
      return 0;
    },
  },
};
</script>
