<template>
  <div>
    <!-- <section>
      <v-container>
        <h3>Presets</h3>
        <v-btn> 0-45-90 </v-btn>
        <v-btn> 60-60-60 </v-btn>
      </v-container>
    </section> -->
    <section>
      <v-container>
        <v-card>
          <v-card-title>Basic Settings</v-card-title>
          <v-card-text>
            <!-- <p>
              Use the settings bellow to configure the AR device, the working
              wavelength is {{ props.wavelength }} nano meters. Due to the
              refractive index of {{ props.material }} of the waveguide, the
              critical angle of total internal reflection is calculated to be
              {{ TIR }} radians, and is denoted by the inner circle of the
              K-Space figure. The diagonal field of view (FOV) is
              {{ props.FOV }} degrees, with screen aspect ratio of
              {{ props.aspect[0] }} : {{ props.aspect[1] }}. Therfore, the
              incident k-vectors are know and is denoted by the gray area in the
              center of the k-space figure.
            </p> -->
            <v-row class="my-0">
              <v-col cols="6" md="2" class="py-0">
                <v-text-field
                  label="Wavelength"
                  suffix="nm"
                  v-model.number="props.wavelength"
                ></v-text-field>
              </v-col>
              <v-col cols="6" md="2" class="py-0">
                <v-text-field
                  label="Refractive Index"
                  v-model.number="props.material"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="5" class="py-0">
                <v-slider
                  label="FOV"
                  v-model="props.FOV"
                  min="0"
                  max="150"
                  tick="1"
                  thumb-label
                >
                  <template v-slot:append>
                    <v-text-field
                      dense
                      v-model.number="props.FOV"
                      class="mt-0 pt-0"
                      suffix="degrees"
                    ></v-text-field>
                  </template>
                </v-slider>
              </v-col>
              <v-col cols="12" sm="6" md="3" class="py-0">
                <v-row>
                  <v-col>
                    <v-text-field
                      label="Aspect"
                      v-model.number="props.aspect[0]"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-text-field
                      v-model.number="props.aspect[1]"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-row class="my-n4">
              <v-col cols="12" sm="6" class="py-0">
                <v-slider
                  label="x offset"
                  dense
                  v-model="props.offset[0]"
                  min="-1"
                  max="1"
                  step="0.1"
                  thumb-label
                >
                  <template v-slot:append>
                    <v-text-field
                      dense
                      v-model.number="props.offset[0]"
                      class="mt-0 pt-0"
                      style="width: 60px"
                    ></v-text-field>
                  </template>
                </v-slider>
              </v-col>
              <v-col cols="12" sm="6" class="py-0">
                <v-slider
                  label="y offset"
                  dense
                  v-model="props.offset[1]"
                  min="-1"
                  max="1"
                  step="0.1"
                >
                  <template v-slot:append>
                    <v-text-field
                      dense
                      v-model.number="props.offset[1]"
                      class="mt-0 pt-0"
                      thumb-label
                      style="width: 60px"
                    ></v-text-field>
                  </template>
                </v-slider>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-container>
    </section>
    <section>
      <v-container>
        <v-row>
          <v-col sm="6">
            <v-card>
              <v-card-title>K Space </v-card-title>
              <v-card-text>
                <p>Drag to modify grating parameters.</p>
              </v-card-text>
              <KSpace
                :g1.sync="props.g1"
                :g3.sync="props.g3"
                :k0="k0"
                :k1="k1"
                :k2="k2"
                :k3="k3"
                :material="props.material"
              ></KSpace>

              <v-card-text>
                <div>
                  <v-row>
                    <v-col cols="2"></v-col>
                    <v-col><v-label>Period</v-label></v-col>
                    <v-col><v-label>Angle</v-label></v-col>
                  </v-row>

                  <v-row class="mt-n4">
                    <v-col cols="2" class="text-right mt-1">
                      <v-label> In </v-label>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g1_period"
                        class="ma-0 pa-0"
                        @change="grating_period_update('g1', $event)"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g1_angle"
                        class="ma-0 pa-0"
                        @change="grating_angle_update('g1', $event)"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row class="mt-n8">
                    <v-col cols="2" class="text-right mt-1">
                      <v-label> EPE </v-label>
                    </v-col>

                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g2_period"
                        class="ma-0 pa-0"
                        readonly
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g2_angle"
                        class="ma-0 pa-0"
                        readonly
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row class="mt-n8">
                    <v-col cols="2" class="text-right mt-1">
                      <v-label> Out </v-label>
                    </v-col>

                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g3_period"
                        class="ma-0 pa-0"
                        @change="grating_period_update('g3', $event)"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        v-model.number="g3_angle"
                        class="ma-0 pa-0"
                        @change="grating_angle_update('g3', $event)"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </div>
                <p>Note: EPE parameters are automatically calculated.</p>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col sm="6">
            <v-card>
              <v-card-title> Layout </v-card-title>
              <v-card-text>
                <p>Drag to modify layout placement.</p>
              </v-card-text>
              <Layout
                :in_pos.sync="props.in_pos"
                :in_size.sync="props.in_size"
                :in_shape1="in_shape1"
                :in_shape2="in_shape2"
                :out_pos.sync="props.out_pos"
                :out_size.sync="props.out_size"
                :out_shape1="out_shape1"
                :out_shape2="out_shape2"
                :epe_shape="epe_shape"
                :background_image="background_image"
              ></Layout>
              <v-card-text>
                <div>
                  <v-row>
                    <v-col cols="2"></v-col>
                    <v-col><v-label>x</v-label></v-col>
                    <v-col><v-label>y</v-label></v-col>
                    <v-col><v-label>z</v-label></v-col>
                  </v-row>
                  <v-row class="mt-n4">
                    <v-col cols="2" class="text-right mt-1"
                      ><v-label>In</v-label></v-col
                    >
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.in_pos[0]"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.in_pos[1]"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.in_displace"
                      >
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="mt-n8">
                    <v-col cols="2" class="text-right mt-1"
                      ><v-label>Out</v-label></v-col
                    >
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.out_pos[0]"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.out_pos[1]"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        dense
                        class="ma-0 pa-0"
                        v-model.number="props.out_displace"
                      >
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="mt-n4">
                    <v-col>
                      <v-row class="mt-n4">
                        <v-col>
                          <v-select
                            :items="shape_types"
                            label="In shape"
                            dense
                            v-model="props.in_shape_type"
                          ></v-select>
                        </v-col>
                      </v-row>
                      <v-row class="mt-n4">
                        <v-col>
                          <v-text-field
                            dense
                            label="width"
                            class="ma-0 pa-0"
                            v-model.number="props.in_size[0]"
                          >
                          </v-text-field>
                        </v-col>
                        <v-col>
                          <v-text-field
                            dense
                            label="height"
                            class="ma-0 pa-0"
                            v-model.number="props.in_size[1]"
                          >
                          </v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                    <v-col>
                      <v-row class="mt-n4">
                        <v-col>
                          <v-select
                            :items="shape_types"
                            label="Out shape"
                            dense
                            v-model="props.out_shape_type"
                          ></v-select> </v-col
                      ></v-row>
                      <v-row class="mt-n4">
                        <v-col>
                          <v-text-field
                            dense
                            label="width"
                            class="ma-0 pa-0"
                            v-model.number="props.out_size[0]"
                          >
                          </v-text-field>
                        </v-col>
                        <v-col>
                          <v-text-field
                            dense
                            label="height"
                            class="ma-0 pa-0"
                            v-model.number="props.out_size[1]"
                          >
                          </v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </section>
    <section>
      <v-container>
        <v-card>
          <v-card-title> Customization </v-card-title>
          <v-card-text>
            <v-row>
              <v-col>
                <v-file-input
                  label="Background Image"
                  dense
                  @change="update_background_image"
                ></v-file-input>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-container>
    </section>
    <section>
      <v-container>
        <v-card>
          <v-card-title> Parameters </v-card-title>
          <v-card-text>
            <template v-for="prop in Object.keys(props)">
              <v-row :key="prop" class="mt-n4">
                <v-col cols="4" sm="3" class="text-right">
                  <label :for="prop">{{ prop }}: </label>
                </v-col>
                <v-col>
                  <template v-if="isArray(props[prop])">
                    <span v-for="(item, i) in props[prop]" :key="i">
                      <input
                        :name="prop"
                        type="text"
                        v-model.number="props[prop][i]"
                      />
                    </span>
                  </template>
                  <template v-else>
                    <input
                      :name="prop"
                      type="text"
                      v-model.number="props[prop]"
                    />
                  </template>
                </v-col>
              </v-row>
            </template>
          </v-card-text>
        </v-card>
      </v-container>
    </section>
    <!-- <section>
      <v-container>
        <h3>Computed Value</h3>
        <br />
        <p>Critical Angle: {{ (TIR / Math.PI) * 180 }} degrees</p>
        <p>g2: {{ g2 }}</p>
      </v-container>
    </section> -->

    <div hidden>
      {{ epe_shape_prop }}
    </div>
  </div>
</template>

<script>
import KSpace from "./KSpace.vue";
import Layout from "./Layout.vue";
import { single_promise, throttle } from "./lib/throttle";
import { mag } from "./lib/geo";
import { diffraction, k1, trace_EPE } from "./lib/layout";
import { circular, rectangular } from "./lib/shape";

const g0 = (Math.PI * 2) / 394;

export default {
  name: "HelloWorld",
  components: {
    KSpace,
    Layout,
  },
  props: {},
  data() {
    return {
      props: {
        // waveguide
        wavelength: 530,
        material: 1.827,

        // screen
        FOV: 40,
        aspect: [4, 3],
        offset: [0, 0],

        // kspace
        g1: [g0, 0],
        g3: [0, -g0],

        // layout
        in_size: [4, 4],
        in_displace: 0,
        in_pos: [-25, -15],
        in_shape_type: "circle",
        out_size: [16, 16],
        out_displace: 20,
        out_pos: [5, 15],
        out_shape_type: "circle",
      },
      epe_shape: [[0], [0]],
      in_shape2: [[0], [0]],
      out_shape2: [[0], [0]],
      shape_types: ["circle", "rectangle"],
      background_image: "",
    };
  },
  methods: {
    isArray(a) {
      return a instanceof Array;
    },
    grating_period_update(id, value) {
      if (isNaN(value)) value = 0;

      if (value != 0) {
        let s = (Math.PI * 2) / value / mag(this.props[id]);
        this.props[id][0] *= s;
        this.props[id][1] *= s;
        this.props[id].__ob__.dep.notify();
      }
    },

    grating_angle_update(id, value) {
      if (isNaN(value)) value = 0;
      let s = mag(this.props[id]);
      this.props[id][0] = Math.cos((value * Math.PI) / 180) * s;
      this.props[id][1] = Math.sin((value * Math.PI) / 180) * s;
      this.props[id].__ob__.dep.notify();
    },

    update_epe: throttle(
      single_promise(async function () {
        const vm = this;
        const [in_shape2, epe_shape, out_shape2] = await trace_EPE(
          ...arguments
        );
        vm.in_shape2 = in_shape2;
        vm.epe_shape = epe_shape;
        vm.out_shape2 = out_shape2;
        this.report();
        return 0;
      }),
      1
    ),

    report: throttle(function () {
      window.dataLayer.push({
        event: "interact",
        props: this.props,
      });
    }, 10 * 1000),

    update_background_image(file) {
      if (file) {
        let reader = new FileReader();
        let vm = this;
        reader.onloadend = function () {
          vm.background_image = reader.result;
          console.log(reader.result);
          window.dataLayer.push({
            event: "interact",
            filename: file.name,
          });
        };
        reader.readAsDataURL(file);
      }
    },
  },
  computed: {
    // Kspace basics
    k0() {
      return (Math.PI * 2) / this.props.wavelength;
    },
    k1() {
      return k1(
        this.k0,
        (this.props.FOV / 180) * Math.PI,
        ...this.props.offset,
        ...this.props.aspect
      ).arraySync();
    },

    k2() {
      return diffraction(this.k1, this.props.g1).arraySync();
    },

    k3() {
      return diffraction(this.k2, this.g2).arraySync();
    },

    TIR() {
      return Math.asin(1 / this.props.material);
    },

    // Gratings
    g2() {
      return [
        -this.props.g1[0] - this.props.g3[0],
        -this.props.g1[1] - this.props.g3[1],
      ];
    },

    g1_period: {
      get() {
        return (Math.PI * 2) / mag(this.props.g1);
      },
      set() {},
    },
    g1_angle: {
      get() {
        return (Math.atan2(this.props.g1[1], this.props.g1[0]) * 180) / Math.PI;
      },
      set() {},
    },

    g2_period: {
      get() {
        return (Math.PI * 2) / mag(this.g2);
      },
      set() {},
    },
    g2_angle: {
      get() {
        return (Math.atan2(this.g2[1], this.g2[0]) * 180) / Math.PI;
      },
      set() {},
    },

    g3_period: {
      get() {
        return (Math.PI * 2) / mag(this.props.g3);
      },
      set() {},
    },
    g3_angle: {
      get() {
        return (Math.atan2(this.props.g3[1], this.props.g3[0]) * 180) / Math.PI;
      },
      set() {},
    },

    in_shape1() {
      if (this.props.in_shape_type == "circle")
        return circular(0, 0, ...this.props.in_size, 32).arraySync();
      return rectangular(0, 0, ...this.props.in_size, 1).arraySync();
    },
    out_shape1() {
      if (this.props.out_shape_type == "circle")
        return circular(0, 0, ...this.props.out_size, 32).arraySync();
      return rectangular(0, 0, ...this.props.out_size, 1).arraySync();
    },

    epe_shape_prop() {
      this.update_epe(
        this.props.in_pos,
        this.in_shape1,
        this.props.in_displace,
        this.props.out_pos,
        this.out_shape1,
        this.props.out_displace,
        this.k0,
        this.k1,
        this.k2,
        this.k3
      );
      return 0;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


      