import { mag } from './geo'
import { convshape, convhullshape, rectangular } from './shape'
import * as tf from '@tensorflow/tfjs'

tf.setBackend('cpu')

const k1 = function (k0, fov, offset_x, offset_y, aspect_x, aspect_y) {
    const diagonal = mag([aspect_x, aspect_y])
    const z = diagonal / 2 / Math.tan(fov / 2)

    const dx = aspect_x * 0.5 * offset_x
    const dy = aspect_y * 0.5 * offset_y
    const points = rectangular(+dx, +dy,
        +aspect_x - Math.abs(dx) * 2,
        +aspect_y - Math.abs(dy) * 2, 8)
    tf.dispose([dx, dy])
    return points.div(points.pow(2).sum(0).add(z ** 2).sqrt()).mul(k0)
}

const diffraction = function (k, g) {
    return tf.add(tf.tensor(k), tf.tensor([[g[0]], [g[1]]]))
}

const average_angle = function (angle, axis = null) {
    const x = tf.mean(tf.cos(angle), axis)
    const y = tf.mean(tf.sin(angle), axis)
    return tf.atan2(y, x)
}

const extreme_angle = function (k) {
    const angle = tf.atan2(k[0], k[1])
    const normalized_angle = angle.sub(average_angle(angle)).add(Math.PI).mod(Math.PI * 2).sub(Math.PI)
    const id_max = tf.argMax(normalized_angle).arraySync()
    const id_min = tf.argMin(normalized_angle).arraySync()
    tf.dispose([angle, normalized_angle])
    return [id_max, id_min]
}


const kz = function (k, k0) {
    return tf.sqrt(tf.sub(k0 ** 2, tf.mul(k, k).sum(0)))
}

const k_normz = function (k, k0) {
    return tf.div(k, kz(k, k0))
}


function extremum(values, axis = null) {
    return tf.concat([tf.max(values, axis, true), tf.min(values, axis, true)], axis)
}


const trace_EPE = async function (in_pos, in_shape, in_displace, out_pos, out_shape, out_displace, k0, k1, k2, k3) {
    const din = k_normz(k1, k0).mul(in_displace)
    const dout = k_normz(k1, k0).mul(-out_displace)

    const d_in_pos = tf.expandDims(in_pos, 1).add(din)
    const d_out_pos = tf.expandDims(out_pos, 1).add(dout)

    const dd = tf.sub(d_out_pos, d_in_pos)
    const k2p = tf.mul([k2[1], k2[0]], [[-1], [1]])
    const k3p = tf.mul([k3[1], k3[0]], [[-1], [1]])

    const intersect = d_out_pos.sub(
        tf.cast(k3, "float32")
            .mul(tf.mul(dd, k2p).sum(0))
            .div(tf.mul(k3, k2p).sum(0)))


    const exk3 = extremum(tf.mul(tf.expandDims(in_shape, 1), tf.expandDims(k2p, 2)).sum(0), 1)
        .mul(tf.mul(k2p, k3).sum(0)
            .div(tf.mul(k2p, k2p).sum(0))
            .div(tf.mul(k3, k3).sum(0)).expandDims(1))


    const exk2 = extremum(tf.mul(tf.expandDims(out_shape, 1), tf.expandDims(k3p, 2)).sum(0), 1)
        .mul(tf.mul(k3p, k2).sum(0)
            .div(tf.mul(k3p, k3p).sum(0))
            .div(tf.mul(k2, k2).sum(0)).expandDims(1))

    tf.dispose(
        dd, k2p, k3p, intersect
    )

    const k21 = tf.mul(exk2.gather(0, 1), k2)
    const k22 = tf.mul(exk2.gather(1, 1), k2)
    const k31 = tf.mul(exk3.gather(0, 1), k3)
    const k32 = tf.mul(exk3.gather(1, 1), k3)

    const epe_shape = await convhullshape(tf.concat([
        intersect.add(k21).add(k31),
        intersect.add(k22).add(k31),
        intersect.add(k21).add(k32),
        intersect.add(k22).add(k32),
    ], 1)).array()

    const in_shape2 = await convshape(din, in_shape).array()
    const out_shape2 = await convshape(dout, out_shape).array()


    tf.dispose(
        exk2, exk3, k21, k22, k31, k32
    )

    return [in_shape2, epe_shape, out_shape2]
}



export {
    k1, diffraction, extreme_angle, kz, k_normz, trace_EPE,
}
