            <v-card-text>
              <h3>Abstract</h3>
              <p>
                For a diffractive-waveguide-based augmented reality display, we
                demonstrate a rapid and straightforward method to define the
                most compact layout of an exit pupil expander (EPE) from an
                analysis of diffractive optical elements in k-space. The idea is
                to consider all k-vectors coming from in-coupling to
                out-coupling areas according to the grating theory, tracing out
                the EPE grating region, and with a geometrical modeling of this
                problem to find the optimal layout. For a working wavelength of
                532 nm, 40 degrees field of view display, the solution to pack
                the entire layout into a enclosed circle radius and minimum area
                of EPE is presented. Furthermore, a web-based interactive
                application demonstrating the EPE layout calculation is provided
                as a layout design tool.
              </p>
              <p>
                <strong>Keywords:</strong> Near-eye display, Waveguide,
                Augmented Reality, Exit-Pupil Expander, Diffractive Optical
                Elements.
              </p>
