<template>
  <div ref="base">
    <div class="layout-container">
      <div
        class="layout-canvas-bg"
        :style="{ backgroundImage: 'url(' + background_image + ')' }"
      ></div>

      <div>
        <canvas class="canvas" width="500" height="500" ref="canvas"> </canvas>
      </div>
    </div>

    <div hidden>
      {{ in_props }}
      {{ out_props }}
      {{ epe_props }}
    </div>
  </div>
</template>

<script>
import canvas from "./canvas.js";
import { fabric } from "fabric";
import { throttle } from "./lib/throttle.js";
import { shape2points } from "./lib/shape.js";

export default {
  props: {
    in_pos: Array,
    in_size: Array,
    in_shape1: Array,
    in_shape2: Array,
    out_pos: Array,
    out_size: Array,
    out_shape1: Array,
    out_shape2: Array,
    epe_shape: Array,
    background_image: String,
  },

  mixins: [canvas],

  data() {
    return {
      // typings
      /**@type {fabric.Canvas} */
      canvas: null,
      // shapes
      in_patch1: null,
      in_patch2: null,
      out_patch1: null,
      out_patch2: null,
      epe_patch: null,
    };
  },

  // SETUP -------------------------------------
  mounted() {
    this.canvas.selection = false;

    this.canvas.add(
      (this.in_patch2 = new fabric.Polygon([], {
        fill: "#37474f80",
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.out_patch2 = new fabric.Polygon([], {
        fill: "#2196f380",
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.canvas.add(
      (this.epe_patch = new fabric.Polygon([], {
        fill: "#ff980080",
        originX: "center",
        originY: "center",
        evented: false,
      }))
    );

    this.in_patch1 = this.create_coupling_patch("#37474f", this.in_update);

    this.out_patch1 = this.create_coupling_patch("#2196f3", this.out_update);
  },

  methods: {
    resize() {
      let s = this.$refs.base.getBoundingClientRect().width;
      // s = Math.min(s, 500);
      this.canvas.setWidth(s);
      this.canvas.setHeight(s);
      this.canvas.calcOffset();
      this.canvas.absolutePan(
        new fabric.Point(-this.canvas.width / 2, -this.canvas.height / 2)
      );
    },

    create_coupling_patch(color, callback) {
      let shape = new fabric.Polygon([], {
        fill: color,
        originX: "center",
        originY: "center",
        lockRotation: true,
        // lockScalingX: true,
        // lockScalingY: true,
        // hasControls: false,
        width: 100,
        height: 100,
      })
        .on("modified", callback)
        .on("moving", callback);
      this.canvas.add(shape);

      return shape;
    },

    // update events
    in_update: throttle(function () {
      this.$emit("update:in_pos", [
        this.in_patch1.get("left") / this.scaling,
        -this.in_patch1.get("top") / this.scaling,
      ]);
      this.$emit("update:in_size", [
        this.in_patch1.get("scaleX") * this.in_size[0],
        this.in_patch1.get("scaleY") * this.in_size[1],
      ]);
      this.in_patch1.set("scaleX", 1);
      this.in_patch1.set("scaleY", 1);
    }, 20),

    out_update: throttle(function () {
      this.$emit("update:out_pos", [
        this.out_patch1.get("left") / this.scaling,
        -this.out_patch1.get("top") / this.scaling,
      ]);
      this.$emit("update:out_size", [
        this.out_patch1.get("scaleX") * this.out_size[0],
        this.out_patch1.get("scaleY") * this.out_size[1],
      ]);
      this.out_patch1.set("scaleX", 1);
      this.out_patch1.set("scaleY", 1);
    }, 20),
  },
  computed: {
    scaling() {
      return this.canvas.width / 80;
    },

    in_props() {
      if (!this.in_patch1) return 0;
      this.in_patch1.set("left", this.in_pos[0] * this.scaling);
      this.in_patch1.set("top", -this.in_pos[1] * this.scaling);
      this.in_patch1.setCoords();
      this.in_patch1.set("points", shape2points(this.in_shape1, this.scaling));
      this.updatePolygon(this.in_patch1);

      this.in_patch2.set("left", this.in_pos[0] * this.scaling);
      this.in_patch2.set("top", -this.in_pos[1] * this.scaling);
      this.in_patch2.setCoords();
      this.in_patch2.set("points", shape2points(this.in_shape2, this.scaling));
      this.updatePolygon(this.in_patch2);

      this.canvas.requestRenderAll();
      return 0;
    },

    out_props() {
      if (!this.out_patch1) return 0;
      this.out_patch1.set("left", this.out_pos[0] * this.scaling);
      this.out_patch1.set("top", -this.out_pos[1] * this.scaling);
      this.out_patch1.setCoords();
      this.out_patch1.set(
        "points",
        shape2points(this.out_shape1, this.scaling)
      );
      this.updatePolygon(this.out_patch1);

      this.out_patch2.set("left", this.out_pos[0] * this.scaling);
      this.out_patch2.set("top", -this.out_pos[1] * this.scaling);
      this.out_patch2.setCoords();
      this.out_patch2.set(
        "points",
        shape2points(this.out_shape2, this.scaling)
      );
      this.updatePolygon(this.out_patch2);

      this.canvas.requestRenderAll();
      return 0;
    },

    epe_props() {
      if (!this.out_patch1) return 0;
      this.epe_patch.set("points", shape2points(this.epe_shape, this.scaling));
      this.updatePolygon(this.epe_patch);
      this.canvas.requestRenderAll();
      return 0;
    },
  },
};
</script>

<style lang="scss" scoped>
.layout-container {
  position: relative;
  width: 100%;
}
.layout-canvas-bg {
  position: absolute;
  background-size: contain;
  background-position: center;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0.3;
  pointer-events: none;
}
</style>